import {
    Component, OnInit, Input, ViewChild, Output, EventEmitter,
    TemplateRef, ContentChildren, QueryList, AfterContentInit, AfterViewInit,
    OnChanges, SimpleChanges, Injector, ChangeDetectorRef, ElementRef, Renderer2
} from '@angular/core';
import { MessagerService } from '@farris/ui-messager';
import { TreeTableComponent, TreeNode } from '@farris/ui-treetable';
import { CommonUtils, FarrisTemplateDirective } from '@farris/ui-common';
import { LocaleService } from '@farris/ui-locale';

@Component({
    selector: 'multi-select',
    templateUrl: './multi-select.component.html',
    styleUrls: [
        './multi-select.css'
    ]
})

export class MultiSelectComponent implements OnInit, OnChanges, AfterViewInit, AfterContentInit {

    private allTreeNodes = [];
    private _ds: any = [];
    get dataSource() {
        return this._ds;
    }
    @Input() set dataSource(val: any) {
        val = val  || [];
        this._ds = val;
        if (!this.searchValue) {
            if (val.length) {
                if (val[0].toJSON) {
                    this.copyDataSource = val.map(n => n.toJSON());
                } else {
                    this.copyDataSource = cloneDeep(val);
                }
            }
            this.initRightData(this.selectedId);
        }
    }

    @Input() displayType: 'List' | 'Tree' = 'List';
    /** æ¾ç¤ºå³ä¾§ä¸ç§»ä¸ç§»æé® */
    @Input() enableTargetSort = true;

    @Input() idField: string;

    @Input() textField: string;
    @Input() valueField: string;

    @Input() height = 445;
    @Input() fit = true;
    @Input() noDataMoveMessage = 'è¯·éæ©è¦ç§»å¨çæ°æ®ã';
    /** æ ç»ææ¶ï¼æ¯å¦æ¾ç¤ºçº§èéæ©æ§å¶éé¡¹ */
    @Input() showCascadeControl = false;

    @Input() leftTitle = 'æªéæ©';
    @Input() rightTitle = 'å·²éæ©';
    @Input() selectedId: any;
    @Input() enableSearch = true;
    /** ç®æ æ°æ®æºæ¾ç¤ºcheckbox åè®¸å¤é */
    @Input() showCheckbox = false;
   

    @Output() dataChange = new EventEmitter();
    @Output() selected = new EventEmitter();
    @Output() selectedIdChange = new EventEmitter();

    @Output() beforeMove = new EventEmitter();
    @Output() afterMove = new EventEmitter();

    @Output() sortMove = new EventEmitter();

    @ContentChildren(FarrisTemplateDirective) templates: QueryList<FarrisTemplateDirective>;
    @ViewChild('tt') tt: TreeTableComponent;


    // å³ä¾§éä¸­çæ°æ® { [keyVal]: obj, [keyVal]: obj ... }
    _rightCheckeds: any;
    // å·¦ä¾§éä¸­çæ°æ® { [keyVal]: obj, [keyVal]: obj ... }
    _leftCheckeds: any = {};

    // å³ä¾§æ°æ®
    _rightData = [];
    get selections() {
        return this._rightData;
    }
    set selections(val: any) {
        this._rightData = val;
        this.cd.detectChanges();
    }
    get searchValue() {
        return this._searchValue;
    }
    set searchValue(value: string) {
        this._searchValue = value;

        // this.copyDataSource = [...this.dataSource];

        if (value === undefined || value === null || value === '') {
            let leftCheckitems = {...this._leftCheckeds};

            if (this.displayType === 'Tree') {
                this.tt.selections.forEach(n => {
                    leftCheckitems[n.id] = n.data;
                });
            }
            this.dataSource = this.copyDataSource;


            if (this.displayType === 'Tree') {
                setTimeout(() => {
                    const ids = Object.keys(leftCheckitems);
                    if (ids && ids.length) {
                        this.tt.checkedNodes(ids);
                    }
                });
            } else  {
                this._leftCheckeds = {...leftCheckitems};
            }
            
            return;
        }
        if (this.displayType === 'Tree') {
            const newDataSource = this.getTreeDataBySearch(this._searchValue, this.allTreeNodes);
            this.tt.loadData(newDataSource);
        } else {
            this.dataSource = this.getListDataBySearch(this._searchValue, this.copyDataSource);
        }

        if (this.updateTimer1) {
            clearTimeout(this.updateTimer1);
        }
        this.updateTimer1 = setTimeout(() => {
            this.canCheckLength = this.getCanCheckLength();
        });
    }

    canCheckLength = 0;
    leftCount = 0;
    _searchValue = '';
    // å¤å¶ä¸ä»½æ°æ®æº
    private copyDataSource: any;
    // æ å½¢ç»æçæ°æ®length
    treeDataLength: number;
    displayTemplate: TemplateRef<any>;


    emptyData;
    el: ElementRef;
    render: Renderer2;
    localeService: LocaleService;
    commonUtils: CommonUtils;
    // çº§èéæ©æ§å¶éé¡¹
    enableCascase = true;

    private updateTimer1 = null;
    private updateTimer2 = null;

    constructor(private mesagerSer: MessagerService, private injector: Injector, private cd: ChangeDetectorRef) {
        if (!Object.values) {
            Object.values = function (obj) {
                return Object.keys(obj).map(function (e) {
                    return obj[e];
                });
            };
        }
        this.el = this.injector.get<ElementRef>(ElementRef);
        this.render = this.injector.get(Renderer2);
        this.localeService = this.injector.get(LocaleService);

        this.commonUtils = this.injector.get(CommonUtils, null);
    }

    ngOnInit() {
        // this.copyDataSource = this.dataSource ? [...this.dataSource] : [];
        this.emptyData = this.localeService.getValue('multiSelect.emptyData') || 'ææ æ°æ®';
        this.initRightData(this.selectedId);
        this.moveItem();
    }

    ngAfterViewInit() {
        const elt = this.el.nativeElement;
        this.render.addClass(elt, 'row');
        this.render.addClass(elt, 'f-multi-select');
        if (this.fit === true) {
            this.render.addClass(elt, 'f-utils-fill');
            this.render.setStyle(elt, 'height', '100%');
        } else {
            if (!!this.height) {
                this.render.setStyle(elt, 'height', this.height + 'px');
            }
        }

        this.noDataMoveMessage =
            this.noDataMoveMessage === 'è¯·éæ©è¦ç§»å¨çæ°æ®ã' ? this.localeService.getValue('multiSelect.noDataMoveMessage') : this.noDataMoveMessage;

        if (this.tt && this.selectedId) {
            this.tt.checkedNodes(this.selectedId);
        }
    }

    ngAfterContentInit() {
        this.templates.forEach((item) => {
            switch (item.getType()) {
                case 'text':
                    this.displayTemplate = item.template;
                    break;
            }
        });
    }

    ngOnChanges(changes: SimpleChanges) {
        if (changes.dataSource && !changes.dataSource.isFirstChange()) {
            // this.copyDataSource = [...(this.dataSource || [])];
            this._leftCheckeds = {};
            this._rightData = [];
        }

        if (changes.selectedId && !changes.selectedId.isFirstChange()) {
            this.initRightData(changes.selectedId.currentValue);
        }
    }

    getCanCheckLength() {
        let n = 0;
        let total = 0;
        if (this.isTree()) {
            this.tt.serializedValue.forEach(rn => {
                if (rn.node.selectable) {
                    if (this.canChecked(rn.node)) {
                        n++;
                    }
                }
            });
            total = this.tt.serializedValue.length;
        } else {
            this.dataSource.forEach(d => {
                if (this.canChecked(d)) {
                    n++;
                }
            });
            total = this.dataSource.length;
        }
        this.leftCount = total;
        return n;
    }

    /**
     * è·åæç´¢çæ°æ®ï¼Listç»æ
     * @param value æç´¢çå­ç¬¦
     * @param data æ°æ®
     */
    private getListDataBySearch(value: string, data: any[]): any[] {
        const result = [];
        value = '' + value;
        if (data instanceof Array) {
            data.forEach(item => {

                let txt = '';
                let val = '';

                if (this.textField) {
                    txt = '' + this.getText(item);
                }

                if (this.valueField) {
                    val = '' + this.getValue(item);
                }

                if (this.textField && this.valueField) {
                    if (txt.indexOf(value) !== -1 || val.indexOf(value) !== -1) {
                        result.push(item);
                    }
                } else if (this.textField && !this.valueField) {
                    if (txt.indexOf(value) !== -1) {
                        result.push(item);
                    }
                } else if (!this.textField && this.valueField) {
                    if (val.indexOf(value) !== -1) {
                        result.push(item);
                    }
                }
            });
        }
        return result;
    }

    /**
     * è·ååå«valueå¼å¾èç¹
     */
    private getTreeDataByRecursive(value: string, data: any[], newData: any[]) {
        value = '' + value;
        data.forEach(item => {

            let txt = '';
            let val = '';

            if (this.textField) {
                txt = '' + this.getText(item.data);
            }

            if (this.valueField) {
                val = '' + this.getValue(item.data);
            }

            if (this.textField && this.valueField) {
                if (txt.indexOf(value) !== -1 || val.indexOf(value) !== -1) {
                    newData.push(item);
                }
            } else if (this.textField && !this.valueField) {
                if (txt.indexOf(value) !== -1) {
                    newData.push(item);
                }
            } else if (!this.textField && this.valueField) {
                if (val.indexOf(value) !== -1) {
                    newData.push(item);
                }
            }
            if (item.children && item.children.length) {
                this.getTreeDataByRecursive(value, item.children, newData);
            }
        });
    }
    /**
     * è·åæ ¹èç¹
     */
    private getTreeRootNode(data: any[], rootData: any[]) {
        data.forEach(item => {
            if (item.parent === null) {
                rootData.push(item);
            } else {
                this.getTreeRootNode(new Array(item.parent), rootData);
            }
        });
    }

    /**
     * è·åæç´¢çæ°æ®ï¼Treeç»æ
     * @param value æç´¢çå­ç¬¦
     * @param data æ°æ®
     */
    private getTreeDataBySearch(value: string, data: any[]): any[] {
        const rootNodes = [];
        const nodes = [];
        const finalRes = [];
        const uniqueId = [];
        // è·ååå«valueå¼å¾èç¹
        this.getTreeDataByRecursive(value, data, nodes);
        // è·åæ ¹èç¹
        this.getTreeRootNode(nodes, rootNodes);
        // å»é
        rootNodes.forEach(node => {
            if (!uniqueId.some(item => item == node.id)) {
                finalRes.push(node);
                uniqueId.push(node.id);
            }
        });
        return finalRes;
    }

    private initRightData(ids) {
        let dataSource: any = [];

        if (ids && ids.length) {
            if (this.isTree()) {
                this.convertTreeDataToPlainData(this.copyDataSource, dataSource);
                this._rightData = this.getTreeSelectionsById(ids, dataSource);
            } else {
                this._rightData = this.getListSelectionsById(ids, this.copyDataSource);
            }

            this._leftCheckeds = ids.reduce((r, id) => {
                r[id] = this._rightData.find(n => n[this.idField] == id);
                return r;
            }, {});

        }

        if (this.updateTimer2) {
            clearTimeout(this.updateTimer2);
        }

        this.updateTimer2 = setTimeout(() => {
            this.canCheckLength = this.getCanCheckLength();
            if (this.tt) {
                this.tt.checkedNodes(ids, true, false, true);
                this.allTreeNodes = cloneDeep(this.tt.state.rowNodes.map(n => n.node));
            }
        });
    }

    /**
     * @param ids éä¸­æ°æ®çid
     * @param dataSource åå§æ°æ®
     */
    private getListSelectionsById(ids: any, dataSource: any) {
        let result = [];
        const _this = this;
        if ((typeof ids === 'string' && !!ids) || typeof ids === 'number') {
            const reusltObj = dataSource.find(item => {
                return item[_this.idField] === ids;
            });
            if (reusltObj) {
                result.push(reusltObj);
            }
        } else if (Array.isArray(ids)) {
            if (Array.isArray(dataSource)) {
                result = ids.map(id => dataSource.find(item => item[this.idField] === id)).filter(n => n);
            } else {
                result = [];
            }
        } else {
            result = [];
        }
        return result;
    }

    /**
     *
     * @param ids éä¸­æ°æ®çid
     * @param dataSource åå§æ°æ®
     */
    private getTreeSelectionsById(ids: any, dataSource: any) {
        let result = [];
        const _this = this;
        if ((typeof ids === 'string' && !!ids) || typeof ids === 'number') {
            const reusltObj = dataSource.find(item => {
                return item.data[_this.idField] === ids;
            });
            if (reusltObj && reusltObj.data) {
                result.push(reusltObj.data);
            }
        } else if (Array.isArray(ids)) {
            if (Array.isArray(dataSource)) {
                result = ids.map(id => {
                    return dataSource.find(item => item.data[this.idField] === id);
                }).filter(n => n).map(n => n.data);
            } else {
                result = [];
            }
        } else {
            result = [];
        }
        return result;
    }
    /**
     * å¤æ­æ¯å¦éä¸­
     * @param data è¦éªè¯çæ°æ®
     * @param direction éªè¯çæ°æ®æå¨çä½ç½® L: å·¦ä¾§ï¼R: å³ä¾§
     */
    isChecked(data: any, direction: 'L' | 'R'): boolean {
        const keyVal = data[this.idField];
        if (direction === 'L') {
            return (this._leftCheckeds && this._leftCheckeds[keyVal] !== undefined); // || !this.canChecked(data);
        } else {
            if (this._rightCheckeds) {
                return this._rightCheckeds[keyVal] !== undefined;
            } else {
                return false;
            }
        }
    }

    canChecked(item: any) {
        let data = item;

        if (this.isTree()) {
            data = item.data;

            if (item.selectable !== undefined && !item.selectable) {
                return false;
            }
        }

        if (!this.selections || this.selections.length === 0) {
            return true;
        }
        return this.selections.findIndex(c => c[this.idField] === data[this.idField]) === -1;
    }

    onCheckValueChange(ids: string[]) {
        
    }

    isTree(): any {
        return this.displayType === 'Tree';
    }

    leftDataChecked(data: any[]) {
        if (data && data.length) {
            data.forEach(node => {
                let d = node;
                if (this.isTree()) {
                    d = node.data;
                }
                if (this.canChecked(node)) {
                    if (this.isChecked(d, 'L')) {
                        delete this._leftCheckeds[d[this.idField]];
                    } else {
                        if (!this._leftCheckeds) {
                            this._leftCheckeds = {};
                        }
                        this._leftCheckeds[d[this.idField]] = d;
                    }
                }
            });
        }
    }

    hasCheckeds(dir: 'L' | 'R') {
        if (dir === 'L') {
            if (this.isTree()) {
                return this.tt.checkeds && this.tt.checkeds.length || ( this.tt.selections && this.tt.selections.length);
            }

            if (this._leftCheckeds) {
                return Object.keys(this._leftCheckeds).length > 0;
            } else {
                return false;
            }
        } else {
            if (this._rightCheckeds) {
                return Object.keys(this._rightCheckeds).length > 0;
            } else {
                return false;
            }
        }
    }

    rightDataChecked(item, emit = true) {
        const isCheck = this.isChecked(item, 'R');
        if (isCheck) {
            if (this.showCheckbox) {
                delete this._rightCheckeds[item[this.idField]];
            } else {
                this._rightCheckeds = {};
            }
            return;
        } else {
            if (this.showCheckbox) {
                this._rightCheckeds = this._rightCheckeds || {};
            } else {
                this._rightCheckeds = {};
            }
            this._rightCheckeds[item[this.idField]] = item;
        }
        this.cd.detectChanges();
        this.selected.emit({ selected: !isCheck, data: item });
    }

    selectItem(id, emit = true) {
        if (this.selections && this.selections.length) {
            const item = this.selections.find(n => n[this.idField] === id);
            if (item) {
                this.rightDataChecked(item, emit);
            }
        }
    }

    private clearSelections() {

    }

    /**
     * å°æ ç»ææ°æ®è½¬æ¢ä¸ºæå¹³æ°ç»
     * @param nodes æ èç¹æ°ç»
     * @param r æ°çç©ºæ°ç»
     */
    private convertTreeDataToPlainData(nodes: TreeNode[], r: any[] = []) {
        if (nodes) {
            nodes.forEach(n => {
                r.push(n);
                if (n.children) {
                    this.convertTreeDataToPlainData(n.children, r);
                }
            });
        }

        return r;
    }

    //#region å·¦å³ç§»å¨

    private moveItem() {
        this.beforeMove.subscribe((obj: any) => {
            const items = obj.items;
            const ids = Object.keys(items);
            let direction = 1;
            switch (obj.target) {
                case 'right':
                    Object.keys(items).forEach(id => {
                        if (!this._rightData.find(n => n[this.idField].toString() === id)) {
                            this._rightData.push(items[id]);
                        }
                    });
                    this._rightData = [...this._rightData];
                    if (this.isTree()) {
                        this.tt.clearAll();
                    }
                    direction = 1;
                    break;
                case 'rightAll':
                    items.forEach(data => {
                        if (this._rightData.findIndex(n => n[this.idField] === data[this.idField]) === -1) {
                            this._rightData.push(data);
                        }
                    });
                    if (this.isTree()) {
                        this.tt.clearAll();
                    }

                    direction = 1;
                    break;
                case 'left':
                    this._rightData = this._rightData.filter(v => {
                        return ids.indexOf('' + v[this.idField]) === -1;
                    });
                    this._rightCheckeds = {};
                    ids.forEach(id => {
                        delete this._leftCheckeds[id];
                    });
                    direction = -1;

                    if (this.isTree()) {
                        this.tt.findRowNodes(ids).forEach(n => {
                            n.node.selectable = true;
                        });
                        this.tt.unCheckedNodes(ids, true);
                    }

                    break;
                case 'leftAll':
                    this._rightCheckeds = {};
                    this._rightData = [];
                    direction = -1;
                    this._leftCheckeds = {};
                    if (this.isTree()) {
                        this.tt.clearAll();
                        this.tt.findRowNodes(ids).forEach(n => {
                            n.node.selectable = true;
                        });
                    }
                    break;
            }

            this.selectionsChange();
            this.changeSelectIds(direction);

            this.afterMove.emit(Object.values(items));
        });
    }


    /** å³ç§» */
    moveToRight(moveData?: any) {

        if (moveData) {
            this.beforeMove.emit({ items: { [moveData[this.idField]]: moveData }, target: 'right' });
        } else {
            if (this.hasCheckeds('L')) {
                if (this.isTree()) {
                    this._leftCheckeds = {};

                    if (!this.tt.checkeds.filter(n => this._rightData.findIndex(r => r.id === n.id) === -1 ).length) {
                        this.mesagerSer.warning(this.noDataMoveMessage);
                        return;        
                    }

                    this.tt.checkeds.forEach(n => {
                        this._leftCheckeds[n.data[this.idField]] = n.data;
                    });

                    this.tt.selections.forEach(n => {
                        this._leftCheckeds[n.id] = n.data;
                    });

                } else {
                    const newCheckeds = Object.keys(this._leftCheckeds).filter(id => {
                        return this._rightData.findIndex(n => n.id === id) === -1
                    });
                    if (!newCheckeds.length) {
                        this.mesagerSer.warning(this.noDataMoveMessage);
                        return;
                    }
                }

                this.beforeMove.emit({ items: this._leftCheckeds, target: 'right' });
            } else {
                this.mesagerSer.warning(this.noDataMoveMessage);
            }
        }
    }

    /** å¨é¨å³ç§» */
    moveAllToRight() {
        if (this.searchValue === '') {
            this._rightData = [];
        }
        if (this.displayType === 'List') {
            const items = this.dataSource;
            // if (this.searchValue && this._leftCheckeds) {
            //     const checkids = Object.keys(this._leftCheckeds);
            //     if (checkids.length) {
            //         checkids.forEach(id => {
            //             if (!items.find(n => n[this.idField] === id)) {
            //                 items.push(this._leftCheckeds[id]);
            //             }
            //         });
            //     }
            // }
            this.beforeMove.emit({ items , target: 'rightAll' });
        } else {
            if (!this.searchValue) {
                let _dataSource = [];
                this.convertTreeDataToPlainData(this.dataSource, _dataSource);
                _dataSource = _dataSource.filter(n => n.selectable && (n.visible || n.visible === undefined)).map(n => n.data);

                this.beforeMove.emit({ items: _dataSource, target: 'rightAll' });
            } else {
                const _dataSource = this.tt.serializedValue.map(n => n.node.data);
                this.beforeMove.emit({ items: _dataSource, target: 'rightAll' });
            }
        }

    }

    /** å·¦ç§» */
    moveToLeft(moveData?: any) {

        if (moveData) {
            this.beforeMove.emit({ items: { [moveData[this.idField]]: moveData }, target: 'left' });
        } else {
            if (this.hasCheckeds('R')) {
                this.beforeMove.emit({ items: this._rightCheckeds, target: 'left' });
            } else {
                this.mesagerSer.warning(this.noDataMoveMessage);
            }
        }
    }

    /** å¨é¨å·¦ç§» */
    moveAllToLeft() {
        this.beforeMove.emit({ items: this._rightData, target: 'leftAll' });
    }

    //#endregion

    //#region ä¸ä¸ç§»å¨æ¾ç¤ºåé¡ºåº

    _getSelectInfo() {
        if (this.hasCheckeds('R')) {
            const key = Object.keys(this._rightCheckeds)[0];
            const index = this._rightData.findIndex(c => '' + c[this.idField] === key);
            return {
                key, index
            };
        }

        return null;
    }
    /** ç½®é¡¶ */
    moveTop() {
        const obj = this._getSelectInfo();
        if (obj) {
            this._rightData.unshift(this._rightData[obj.index]);
            this._rightData.splice(obj.index + 1, 1);
            this.selectionsChange();
            this.changeSelectIds(0);
        } else {
            this.mesagerSer.warning(this.noDataMoveMessage);
        }
    }

    /** ä¸ç§» */
    moveUp() {
        if (this.hasCheckeds('R')) {
            const obj = this._getSelectInfo();
            if (obj) {
                const index = obj.index;
                if (index !== 0) {
                    const tempArr = this._rightData.splice(index, 1);
                    this._rightData.splice(index - 1, 0, ...tempArr);
                }
                this.selectionsChange();
                this.changeSelectIds(0);
            }
        } else {
            this.mesagerSer.warning(this.noDataMoveMessage);
        }
    }

    /** ä¸ç§» */
    moveDown() {
        if (this.hasCheckeds('R')) {
            const obj = this._getSelectInfo();
            if (obj) {
                const len = this._rightData.length;
                const index = obj.index;
                if (index === 0 || index !== len - 1) {
                    const tempArr = this._rightData.splice(index, 1);
                    this._rightData.splice(index + 1, 0, ...tempArr);
                }
                this.changeSelectIds(0);
                this.selectionsChange();
            }
        } else {
            this.mesagerSer.warning(this.noDataMoveMessage);
        }
    }

    /** ç½®åº */
    moveBottom() {
        const obj = this._getSelectInfo();
        if (obj) {
            this._rightData.push(this._rightCheckeds[obj.key]);
            this._rightData.splice(obj.index, 1);
            this.changeSelectIds(0);
            this.selectionsChange();
        } else {
            this.mesagerSer.warning(this.noDataMoveMessage);
        }
    }
    /**
     * è§¦åéæ©ä¸­id
     * @param number 1ä»£è¡¨å·¦ä¾§æ°æ®å³ç§»ï¼-1,ä»£è¡¨æ°æ®å·¦ç§»ï¼0ä»£è¡¨ä¸ç§»ä¸ç§»
     */
    private changeSelectIds(number: number) {
        const ids = this._rightData.map(item => {
            return item[this.idField];
        });

        if (number === 0) {
            if (typeof this.selectedId === 'string') {
                this.selectedId = ids.join(',');
            } else if (Array.isArray(this.selectedId)) {
                this.selectedId = ids;
            }
            this.selectedIdChange.emit(this.selectedId);
            return;
        }

        if (!this.selectedId) {
            this.selectedId = [];
        }

        if (number === 1 || number === -1) {
            if (!ids.length) {
                this.selectedId = [];
            } else {
                this.selectedId = ids;
            }

            if (this.isTree()) {
                if (ids && ids.length) {
                    this.tt.checkedNodes(ids, true, false, true);
                }
            }
        }
        this.canCheckLength = this.getCanCheckLength();
        this.selectedIdChange.emit(this.selectedId);
    }

    //#endregion

    private selectionsChange() {
        this.dataChange.emit(this._rightData);
        this.cd.detectChanges();
    }

    getText(item, textField = this.textField) {
        if (item) {
            if (this.commonUtils && this.commonUtils.getValue && textField) {
                return this.commonUtils.getValue(textField, item);
            } else {
                return item[textField];
            }
        } else {
            return '';
        }
    }

    getValue(item, valueField = this.valueField) {
        if (item) {
            if (this.commonUtils && this.commonUtils.getValue && valueField) {
                return this.commonUtils.getValue(valueField, item);
            } else {
                return item[valueField];
            }
        } else {
            return '';
        }
    }

    trackByItems = (index, item) => {
        return index;
    }

}


@Component({
    selector: 'display-text',
    template: `
        <ng-container *ngIf="template" >
            <ng-container
                *ngTemplateOutlet="template; context: {$implicit: data,instance: ms, side: side, selected: ms.isChecked(data, 'R') }">
            </ng-container>
        </ng-container>
        <ng-container *ngIf="!template">
            {{ _getText(data) }}
        </ng-container>
    `
})
export class DisplayTextComponent implements OnInit {
    @Input() template: TemplateRef<any>;
    @Input() data: any;
    @Input() side = 'L';

    constructor(public ms: MultiSelectComponent) { }

    ngOnInit() {
        // console.log(this.data);
    }

    _getText(item) {
        return this.ms.getText(item);
    }
}
